<script>
import moment from "moment";
export default {
    data() {
        var validateIdNumber = (rule, value, callback) => {
            if (!value) {
                return callback(new Error("请输入身份证号"));
            }
            let id = value;
            var format = /^(([1][1-5])|([2][1-3])|([3][1-7])|([4][1-6])|([5][0-4])|([6][1-5])|([7][1])|([8][1-2]))\d{4}(([1][9]\d{2})|([2]\d{3}))(([0][1-9])|([1][0-2]))(([0][1-9])|([1-2][0-9])|([3][0-1]))\d{3}[0-9xX]$/;
            //号码规则校验
            if (!format.test(id)) {
                return callback(new Error("身份证校验码不合规"));
            }
            //区位码校验
            //出生年月日校验   前正则限制起始年份为1900;
            var year = id.substr(6, 4), //身份证年
                month = id.substr(10, 2), //身份证月
                date = id.substr(12, 2), //身份证日
                time = Date.parse(month + "-" + date + "-" + year), //身份证日期时间戳date
                now_time = Date.parse(new Date()), //当前时间戳
                dates = new Date(year, month, 0).getDate(); //身份证当月天数
            if (time > now_time || date > dates) {
                return { status: 0, msg: "出生日期不合规" };
            }
            //校验码判断
            var c = new Array(
                7,
                9,
                10,
                5,
                8,
                4,
                2,
                1,
                6,
                3,
                7,
                9,
                10,
                5,
                8,
                4,
                2
            ); //系数
            var b = new Array(
                "1",
                "0",
                "X",
                "9",
                "8",
                "7",
                "6",
                "5",
                "4",
                "3",
                "2"
            ); //校验码对照表
            var id_array = id.split("");
            var sum = 0;
            for (var k = 0; k < 17; k++) {
                sum += parseInt(id_array[k]) * parseInt(c[k]);
            }
            if (id_array[17].toUpperCase() != b[sum % 11].toUpperCase()) {
                return callback(new Error("身份证校验码不合规"));
            }
            callback();
        };
        var validateBankId = (rule, value, callback) => {
            if (!value) {
                return callback(new Error("请输入银行卡号"));
            }
            let card_number = value;
            // 卡号字符串化并去除空格，仅保留数字
            var str_digits = (card_number + "").replace(/[\D]/g, "");

            // 银行卡号必须为12-19位数字
            if (!/^\d{12,19}$/.test(str_digits)) {
                return callback(new Error("银行卡号不合规"));
            }

            // 根据luhn规则，将卡号数组化，并反转顺序，以便于操作
            var luhn_digits = str_digits.split("").reverse(),
                // 取第1位作为后续的验证号码
                luhn_checkcode = parseInt(luhn_digits.shift(), 10);

            var loop_length = luhn_digits.length,
                loop_index = loop_length;

            var luhn_sum = 0;
            for (; loop_index > 0; loop_index--) {
                var _i = loop_length - loop_index,
                    _k = parseInt(luhn_digits[_i], 10);
                var _add_val = _k;
                // 偶数字段 需要*2，并且大于10的数字要相加2个位数的值
                if (_i % 2 === 0) {
                    var _k2 = _k * 2;
                    switch (_k2) {
                        case 10:
                            _add_val = 1;
                            break;
                        case 12:
                            _add_val = 3;
                            break;
                        case 14:
                            _add_val = 5;
                            break;
                        case 16:
                            _add_val = 7;
                            break;
                        case 18:
                            _add_val = 9;
                            break;
                        default:
                            _add_val = _k2;
                    }
                }
                luhn_sum += _add_val;
            }

            /* 方法1
       1. 从校验位开始，从右往左，偶数位乘2，然后将两位数字的个位与十位相加；
       2. 计算所有数字的和（67）；
       3. 乘以9（603）；
       4. 取其个位数字（3），得到校验位。
     */
            var luhn_sum9 = luhn_sum * 9,
                luhn_sum9_last_code = parseInt(
                    (luhn_sum9 + "").replace(/\d+(\d$)/, "$1"),
                    10
                );
            if (luhn_sum9_last_code === luhn_checkcode) {
                callback();
            } else {
                return callback(new Error("银行卡号不合规"));
            }

            /* 方法2
       1. 从校验位(即不包括该位数)开始，从右往左，偶数位乘2（例如，7*2=14），然后将两位数字的个位与十位相加（例如，10：1+0=1）；
       2. 把得到的数字加在一起；
       3. 将数字的和取模10（本例中得到7），再用10去减（本例中得到3），得到校验位。 
     */
            // var luhn_sum_mod10 = luhn_sum%10,
            //     luhn_sum_checkcode = 10 - luhn_sum_mod10;
            // return (luhn_sum_checkcode===luhn_checkcode);

            /* 方法3
       1. 从校验位(即不包括该位数)开始，从右往左，偶数位乘2（例如，7*2=14），然后将两位数字的个位与十位相加（例如，10：1+0=1）；
       2. 把得到的数字加在一起；
       3. 再加上检验位的数值，将结果取模10，如果余数为0，则符合规则。 
     */
            // return (((luhn_sum+luhn_checkcode)%10) === 0);
        };
        var checkPhone = (rule, value, callback) => {
            const phoneReg = /^1[3|4|5|7|8][0-9]{9}$/;
            if (!value) {
                return callback(new Error("请输入电话号码"));
            }
            setTimeout(() => {
                // Number.isInteger是es6验证数字是否为整数的方法,实际输入的数字总是识别成字符串
                // 所以在前面加了一个+实现隐式转换

                if (!Number.isInteger(+value)) {
                    callback(new Error("请输入数字值"));
                } else {
                    if (phoneReg.test(value)) {
                        callback();
                    } else {
                        callback(new Error("电话号码格式不正确"));
                    }
                }
            }, 100);
        };
        var checksPhone = (rule, value, callback) => {
            const phoneReg = /^[0-9]{6}$/;
            if (!value) {
                return callback(new Error("请输入电话号码"));
            }
            setTimeout(() => {
                // Number.isInteger是es6验证数字是否为整数的方法,实际输入的数字总是识别成字符串
                // 所以在前面加了一个+实现隐式转换

                if (!Number.isInteger(+value)) {
                    callback(new Error("请输入数字值"));
                } else {
                    if (phoneReg.test(value)) {
                        callback();
                    } else {
                        callback(new Error("电话号码格式不正确"));
                    }
                }
            }, 100);
        };
        var checkEmail = (rule, value, callback) => {
            const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
            if (!value) {
                return callback(new Error("请输入邮箱"));
            }
            setTimeout(() => {
                if (mailReg.test(value)) {
                    callback();
                } else {
                    callback(new Error("请输入正确的邮箱格式"));
                }
            }, 100);
        };

        return {
            ds:false,
            activeName: "first",
            msgForm: {
                wid: "2019500", //工号
                name: "张三", //姓名
                sex: "0", //性别
                nation: "汉族", //民族
                nationality: "中国", //国籍
                origin: "广东省东莞市", //籍贯
                idType: "0", //证件类型
                idNumber: "432503197505028819", //证件号
                education: "3", //学历
                degree: "0", //学位
                department: "0", //部门
                job: "清洁工", //岗位
                title: "工人", //职称
                lPhone: "12547896321", //手机号码
                sPhone: "12345", //短号
                email: "124452@qq.com", //邮箱
                img: "https://i.loli.net/2019/07/12/5d28410b6a20524513.jpg", //头像
                state: 1 //状态
            },
            msgrules: {
                name: [
                    { required: true, message: "请输入姓名", trigger: "blur" }
                ],
                sex: [
                    { required: true, message: "请选择性别", trigger: "blur" }
                ],
                nation: [
                    { required: true, message: "请输入民族", trigger: "blur" }
                ],
                nationality: [
                    { required: true, message: "请输入国籍", trigger: "blur" }
                ],
                idType: [
                    {
                        required: true,
                        message: "请选择开户银行",
                        trigger: "blur"
                    }
                ],
                idNumber: [
                    {
                        required: true,
                        validator: validateIdNumber,
                        trigger: "blur"
                    }
                ],
                education: [
                    { required: true, message: "请选择学历", trigger: "blur" }
                ],
                degree: [
                    { required: true, message: "请选择学位", trigger: "blur" }
                ],
                department: [
                    { required: true, message: "请选择部门", trigger: "blur" }
                ],
                lPhone: [
                    {
                        required: true,
                        validator: checkPhone,
                        trigger: "blur"
                    }
                ],
                sPhone: [
                    {
                        validator: checksPhone,
                        trigger: "blur"
                    }
                ],
                email: [
                    {
                        validator: checkEmail,
                        trigger: "blur"
                    }
                ]
            },

            fileListZ: [
                {
                    name: "201945412224zjz.jpg",
                    url:
                        "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
                }
            ],
            fileListF: [
                {
                    name: "201945412224zjf.jpg",
                    url:
                        "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
