                        validator: validateBankId,
                        trigger: "blur"
                    }
                ]
            },

            htForm: {},
            htTable: [
                {
                    name: "合同1", //合同名称
                    signingTime: "2019-06-20", //签约时间
                    useTime: 24, //合同有效期
                    state: 1 //状态
                },
                {
