            search: "",
            ht: 0
        };
    },
    created: function() {
        let wid = this.$route.params.wid;
        let index = this.$route.params.index;
        this.show = wid;
        if (wid === "0") {
            this.ds = true;
            this.msgForm = { wid: "2019507", name: "" };
            this.fileListZ = [];
            this.fileListF = [];
            this.xzForm = {};
            this.htForm = {};
            this.htTable = [];
        }
        if(index===1) this.activeName = 'first';
        else if(index===2) this.activeName = 'second';
        else this.activeName = 'third';
    },
    methods: {
        handleClick(tab, event) {
            console.log(tab, event);
        },
        goBack() {
            this.$router.go(-1);
        },
        //删除证件
        beforeRemove(file, fileList) {
            return this.$confirm(`确定移除 ${file.name}？`);
        },
        //头像上传成功回调
        handleAvatarSuccess(res, file) {
            this.msgForm.img = URL.createObjectURL(file);
        },
        //头像上传前调用
        beforeAvatarUpload(file) {
            // 上传图片前处理函数
            const isJPG =
                file.type === "image/jpeg" || file.type === "image/png";
            const isLt2M = file.size / 1024 < 500;
            let that = this;
            let isAllow = false;
            if (!isJPG) {
                this.$message.error("上传头像图片只能是 jpg、png格式!");
            }
            if (!isLt2M) {
                this.$message.error("上传头像图片大小不能超过 500KB!");
            }
            const isSize = new Promise(function(resolve, reject) {
                let width = 144;
                let height = 192;
                let _URL = window.URL || window.webkitURL;
                let image = new Image();
                image.onload = function() {
                    let valid = image.width == width && image.height == height;
                    valid ? resolve() : reject();
                };
                image.src = _URL.createObjectURL(file);
            }).then(
                () => {
                    return file;
                },
                () => {
                    this.$message.error(
                        "上传头像图片尺寸不符合,只能是144*192!"
                    );
                    return Promise.reject();
                }
            );
            return isJPG && isLt2M && isSize;
        },
        //证件上传前调用
        beforeUpload(file) {
            // 上传图片前处理函数
            const isJPG =
                file.type === "image/jpeg" || file.type === "image/png";
            const isLt2M = file.size / 1024 < 500;
            let that = this;
            let isAllow = false;
            if (!isJPG) {
                this.$message.error("上传头像图片只能是 jpg、png格式!");
            }
            if (!isLt2M) {
                this.$message.error("上传头像图片大小不能超过 500KB!");
            }
            return isJPG && isLt2M;
        },
        //添加合同
        add() {},
        //删除合同
        handleDelete(index, row) {
            this.$confirm(`确定移除 ${row.name}？`, "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.$message({
                    type: "success",
                    message: "删除成功!"
                });
                for (let i in this.htTable) {
                    if (this.htTable[i] == row) {
                        this.htTable.splice(i, 1);
                    }
                }
            });
        },
        //重置合同表单
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.$refs.htupload.clearFiles();
            this.ht = 0;
        },
        //重置新增表单
        xzreset() {
            this.xzForm =
                this.$route.params.wid === "0"
                    ? {}
                    : {
                          baseWage: "12345", //基本工资
                          bankName: "0", //银行名称
                          bankId: "6222020903001483077" //银行账号
                      };
        },
        //重置信息表单
        msgreset() {
            this.msgForm =
                this.$route.params.wid === "0"
                    ? { wid: "2019507", name: "" }
                    : {
                          wid: "2019500", //工号
                          name: "张三", //姓名
                          sex: "0", //性别
                          nation: "汉族", //民族
                          nationality: "中国", //国籍
                          origin: "广东省东莞市", //籍贯
                          idType: "0", //证件类型
                          idNumber: "432503197505028819", //证件号
                          education: "3", //学历
                          degree: "0", //学位
                          department: "0", //部门
                          job: "清洁工", //岗位
                          title: "工人", //职称
                          lPhone: "12547896321", //手机号码
                          sPhone: "12345", //短号
                          email: "124452@qq.com", //邮箱
                          img:
                              "https://i.loli.net/2019/07/12/5d28410b6a20524513.jpg", //头像
                          state: 1 //状态
                      };
        },
        //表单中有合同
        htlai(file, fileList) {
            const isJPG = file.type === "image/jpeg";
            const isPNG = file.type === "image/png";
            const isLt500k = file.size / 1024 < 500;
            if (!isLt500k) {
                this.$message.error("上传合同图片大小不能超过 500k!");
                return false;
            }
            if (!(isJPG || isPNG)) {
                this.$message.error("上传合同图片的格式必须是JPG或PNG!");
                return false;
            }
            this.ht = 1;
        },
        htclaer() {
            this.$refs.htupload.clearFiles();
        },
        //表单移除合同
        htzou(file, fileList) {
            this.ht = 0;
        },
        //合同上传完成
        htwc() {},
        //提交合同
        htSubmit() {
            if (
                this.htForm.name != null &&
                this.htForm.useTime != null &&
                this.ht != 0
            ) {
                this.$message({
                    type: "success",
                    message: "增加成功!"
                });
                this.htForm.signingTime = moment(new Date()).format(
                    "YYYY-MM-DD"
                );
                this.htForm.state = 1;
                this.htTable.push(this.htForm);
            } else {
                this.$message.error("信息不全");
            }
        },
        msgSubmit() {
            this.$refs.msgForm.validate(valid => {
                if (valid) {
                    this.$message({
                        type: "success",
                        message: "提交成功!"
                    });
                    this.ds=false;
                }
            });
        },
        //薪酬表单提交
        xzSubmit() {
            this.$refs.xzForm.validate(valid => {
                if (valid) {
                    this.$message({
                        type: "success",
                        message: "提交成功!"
                    });
                }
            });
        }
    }
};
</script>
<style lang="stylus" scoped>
.el-page-header {
    margin-bottom: 10px;
}

.main {
    background: #ffffff;
    padding: 20px;
}

.el-select {
    width: 100%;
}

.img {
    text-align: center;
}

.htfile {
    margin-left: 10px;
}

.el-divider__text {
    font-size: 12px;
    color: #606266;
}
</style>



